*,
*::after,
*::before {
    box-sizing: border-box;
}
html{
    font-size:10px;
    @media (max-width:1136px) {
        font-size:8px;
    } 
     @media (max-width:768px) {
        font-size:10px;
    } 
}
blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
    margin: 0;
}

body {
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    scroll-behavior: smooth;
    text-rendering: optimizeSpeed;
    overflow-x: hidden;
    height: 100%;
    font-size:1.6rem;
    line-height: 1.5em;
    background: #F5F5F5;
}

ol,
ul {
    padding: 0;
    list-style: none;
}

a {
    font-family: inherit;
    text-decoration: none;
    color: inherit;
}

img {
    max-width: 100%;
    height: auto;
}

button {
    cursor: pointer;
    border: none;
}

button,
input,
select,
textarea {
    font-family: inherit;
}
